  --error-color: #ff001b;
  --footer-color: #f7f7f7;
  --time-function-primary: linear;
  --time-function-secondary: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
